import { navigator } from '../utils/index'

/**
 * @param    {boolean}    ISDEBUG         
 * @param    {string}     NAVIGATOR       : IOSAndroid
 */

const ISDEBUG = (process.env.NODE_ENV === 'development')
const NAVIGATOR = navigator()

export {
  ISDEBUG,
  NAVIGATOR
}



